import TweenMax from 'TweenMax'
import ScrollToPlugin from 'ScrollToPlugin'
import ScrollMagic from 'ScrollMagic'
import 'animation.gsap'
import 'debug.addIndicators'
import { select, selectAll } from '../libs/selectors'

function MainFeatures(el, options = {}) {
  let blocks = document.querySelectorAll(el)
  let inited = false
  let controller = new ScrollMagic.Controller()
  return { init }

  function init() {
    if (blocks.length === 0 || inited) {
      return
    }

    update()
  }

  function update() {
    for (const block of blocks) {
      let title = select('.title', block)
      let list = selectAll('.features-list li', block)

      let scene
      let tl = new TimelineMax()

      tl.from(title, 1, { y: 40, opacity: 0 }, 0.2)
        .staggerFrom(list, 1, { scale: 0.9, y: 40, opacity: 0 }, 0.4)

      scene = new ScrollMagic.Scene({
        triggerElement: block,
        triggerHook: 0.4,
        duration: window.innerHeight / 4,
        offset: 0
      })
        .setTween(tl)
        // .addIndicators({ name: 'animated scene2' })
        .addTo(controller)
    }
  }

}

export default MainFeatures
