export const getMq = () => {
        return window.getComputedStyle(document.querySelector('body'), '::before')
                .getPropertyValue('content').replace(/\"/g, '') // eslint-disable-line no-useless-escape
}

export const getScrollPos = () => {
  return window.pageYOffset || document.documentElement.scrollTop
}

export const getWinHeight = () => {
  return isNaN(window.innerHeight) ? window.clientHeight : window.innerHeight
}

export const getWinWidth = () => {
  return isNaN(window.innerWidth) ? window.clientWidth : window.innerWidth
}
