import TweenMax from 'TweenMax'
import ScrollToPlugin from 'ScrollToPlugin'
import ScrollMagic from 'ScrollMagic'
import 'animation.gsap'
import 'debug.addIndicators'
import { getMq } from '../libs/helpers'

function ImgText(el, options = {}) {
  let els = document.querySelectorAll(el)
  let inited = false
  let controller
  let scene
  let currentBreakpoint = null

  return { init, update }

  function init() {
    if (els.length === 0 || inited) {
      return
    }

    update()
  }

  function update() {
    if (currentBreakpoint !== getMq()) {
      currentBreakpoint = getMq()

      if (controller) {
        controller.destroy(true)
        controller = null
        scene.destroy()
        scene = null
      }
      set()
    }
  }

  function set() {
    controller = new ScrollMagic.Controller()
    for (const el of els) {
      let texts = el.querySelectorAll('[anim-text]')
      let img = el.querySelectorAll('[anim-img]')
      let direction
      let tl = new TimelineMax()

      TweenLite.set([texts, img], { clearProps: "all" })

      if (currentBreakpoint === 'desktop') {
        if (img && img[0].dataset.direction === 'right') {
          direction = '40'
        } else {
          direction = '-40'
        }

        tl.fromTo(img, 1.5, { x: direction, opacity: 0 }, { x: 0, opacity: 1 })
          .staggerFrom(texts, 0.5, { scale: 0.95, y: 40, opacity: 0 }, 0.2, '-=0.2')

      } else {
        tl.staggerFrom(texts, 1, { y: 20, opacity: 0 }, 0.2)
          .fromTo(img, 1.5, { y: 20, opacity: 0 }, { y: 0, opacity: 1 }, '-=0.2')
      }
      scene = new ScrollMagic.Scene({
        triggerElement: el,
        triggerHook: 0.6,
        duration: window.innerHeight / 2,
        offset: 0
      })
        .setTween(tl)
        .addTo(controller)
    }
  }

}

export default ImgText
