import TweenMax from 'TweenMax'
import ScrollToPlugin from 'ScrollToPlugin'
import ScrollMagic from 'ScrollMagic'
import 'animation.gsap'
import 'debug.addIndicators'
import { getMq } from '../libs/helpers'

function StickySection(el, options = {}) {
  let sections = document.querySelectorAll(el)
  let inited = false
  let controller
  let currentBreakpoint
  return { init, update }

  function init() {
    if (sections.length === 0 || inited) {
      return
    }
    inited = true

    update()
  }

  function update() {
    currentBreakpoint = getMq()
    
    if (!inited) {
      return
    }
    if (currentBreakpoint === 'mobile') {
      if (controller) {
        controller.destroy(true)
        controller = null
      }
      return
    } else {
      if (!controller) {
        controller = new ScrollMagic.Controller()
        setForDesktop()
      }
    }
  }

  function setForDesktop() {
    for (const section of sections) {
      let borders = {}
      let scene
      let tl = new TimelineMax()
      let img = section.querySelectorAll('.background-img')

      borders.left = section.querySelectorAll('.section-border-left')
      borders.right = section.querySelectorAll('.section-border-right')
      borders.top = section.querySelectorAll('.section-border-top')
      borders.bottom = section.querySelectorAll('.section-border-bottom')

      tl.fromTo([borders.left, borders.right], 1, { scaleX: 1 }, { scaleX: 0 })
        .fromTo([borders.top, borders.bottom], 1, { scaleY: 1 }, { scaleY: 0 }, '-=1')
        .fromTo(img, 1, { scale: 1.03 }, { scale: 1 }, '-=1')

      scene = new ScrollMagic.Scene({
        triggerElement: section,
        triggerHook: 0,
        duration: 200,
        offset: -65
      })
        .setTween(tl)
        // .addIndicators({ name: 'animated scene' })
        .addTo(controller)

      scene.on('progress', function (e) {
        if (e.progress > 0) {
          TweenLite.set(section, { className: '+=active' })
        } else {
          TweenLite.set(section, { className: '-=active' })
        }
      })
    }
  }

}

export default StickySection
