import '../styles/index.scss'
'use strict'

import { Manager, BREAKPOINT_CHANGE, WINDOW_WAS_RESIZED, WINDOW_DID_SCROLL } from './libs/events'
import { select, selectAll } from './libs/selectors'
import { throttle, debounce } from 'lodash'
import IntroSection from './modules/introSection'
import CatalogSection from './modules/catalogSection'
import StickySection from './modules/stickySection'
import TextBlock from './modules/textBlock'
import ImgText from './modules/imgText'
import MainFeatures from './modules/mainFeatures'
import { getMq, getScrollPos, getWinHeight } from './libs/helpers'


let introSection = new IntroSection('[js-intro-section]')
let catalogSection = new CatalogSection('[js-catalog-section]')
let stickySections = new StickySection('[js-sticky-section]')
let textBlocks = new TextBlock('[js-text-block]')
let ImgTexts = new ImgText('[js-img-text]')
let mainFeatures = new MainFeatures('[js-main-features]')
let vhHeightsEls
let windowHeight
let currentBreakpoint
let lastScrollTop = 0
let header = select('header')

document.addEventListener("DOMContentLoaded", function () {
    currentBreakpoint = getMq()
    vhHeightsEls = document.querySelectorAll('[js-vh]')

    function navBarHandler() {
      const currScroll = getScrollPos()
      if (currScroll > 10) {
        header.classList.add('fixed')
      } else {
        header.classList.remove('fixed')
      }
      if (currentBreakpoint === 'desktop') {
        header.classList.remove('hidden')
      } else {
        if (currScroll > lastScrollTop) {
          header.classList.add('hidden')
        } else {
          header.classList.remove('hidden')
        }
      }
      lastScrollTop = currScroll <= 0 ? 0 : currScroll
    }
    navBarHandler()


    let setHeights = (update) => {
      windowHeight = getWinHeight()
      let defaultOptions = {
        "options": {
          "mobile": {
            "enabled": true,
            "resize": false,
          },
          "tablet": {
            "enabled": true,
            "resize": false
          },
          "desktop": {
            "enabled": true,
            "resize": true
          }
        }
      }

      let defautSizes = {
        "sizes": {
          "mobile": 100,
          "tablet": 100,
          "desktop": 100
        }
      }

      for (const section of vhHeightsEls) {
        let opts = section.getAttribute('js-vh')
        let sizes = section.getAttribute('js-vh-val')
        let heightValue = windowHeight / 100

        opts = opts ? JSON.parse(opts) : defaultOptions
        sizes = sizes ? JSON.parse(sizes) : defautSizes

        // check if we have to apply on different breakpoints
        let shouldApply = (currentBreakpoint === 'mobile' && opts.options.mobile.enabled) ||
          (currentBreakpoint === 'tablet' && opts.options.tablet.enabled) ||
          (currentBreakpoint === 'desktop' && opts.options.desktop.enabled)

          //get height value
        if (currentBreakpoint === 'mobile') {
          heightValue = heightValue * sizes.sizes.mobile
        } else if (currentBreakpoint === 'tablet') {
          heightValue = heightValue * sizes.sizes.tablet
        } else if (currentBreakpoint === 'desktop') {
          heightValue = heightValue * sizes.sizes.desktop
        }

        if (update) {
          if (shouldApply && (opts.options.mobile.resize || opts.options.tablet.resize || opts.options.desktop.resize)) {
            TweenLite.set(section, { minHeight: heightValue })
          } else {
            TweenLite.set(section, { clearProps: "all" })
          }
        } else {
          if (shouldApply) {
            TweenLite.set(section, { minHeight: heightValue })
          } else {
            TweenLite.set(section, { clearProps: "all" })
          }
        }
      }
    }
    // Listen for resize events
    function resizeEventhandler() {
      currentBreakpoint = getMq()
      setHeights(true)
      catalogSection.update()
      stickySections.update()
      ImgTexts.update()
    }

    // init
    setHeights()
    introSection.init()
    catalogSection.init()
    stickySections.init()
    textBlocks.init()
    ImgTexts.init()
    mainFeatures.init()

    function initInputAnimation() {
      let inputs = selectAll('input')

      for (const input of inputs) {
        input.addEventListener("focus", function (e) {
          e.target.closest('.form-group').classList.add('focused')
        })
        input.addEventListener("blur", function (e) {
          e.target.closest('.form-group').classList.add('focused')
          let input = e.target
          let inputValue = e.target.value
          if (inputValue == "") {
            input.closest('.form-group').classList.remove('focused')
          }
        })
      }
    }

    initInputAnimation()


    Manager.subscribe(WINDOW_DID_SCROLL, navBarHandler)

    Manager.subscribe(WINDOW_WAS_RESIZED, resizeEventhandler)

    // Listen for orientation change events
    window.addEventListener('orientationchange', debounce(e => {
      if (getMq() !== 'desktop') {
        setHeights()
      }
    }, 20), false)
  })