import TweenMax from 'TweenMax'
import ScrollToPlugin from 'ScrollToPlugin'
import ScrollMagic from 'ScrollMagic'
import 'animation.gsap'
import 'debug.addIndicators'
import { getMq } from '../libs/helpers'
import { select, selectAll } from '../libs/selectors'

function CatalogSection(el, options = {}) {
  let sections = document.querySelectorAll(el)
  let inited = false
  let controller
  let currentBreakpoint
  let oldProgress = 1
  let currentProgress = 1
  return { init, update }

  function init() {
    if (sections.length === 0 || inited) {
      return
    }
    inited = true

    update()
  }

  function update() {
    currentBreakpoint = getMq()
    if (!inited) {
      return
    }

    if (!controller) {
      controller = new ScrollMagic.Controller()
      set()
    } else {
      controller.destroy(true)
      controller = new ScrollMagic.Controller()
      set()
    }
  }

  function set() {
    let speed = "0.8"
    for (const section of sections) {
      let scene
      let tl = new TimelineMax()
      let sectionHeight = section.offsetHeight

      scene = new ScrollMagic.Scene({
        triggerElement: section,
        triggerHook: 0,
        duration: sectionHeight,
        offset: 0
      })
        .addTo(controller)


      scene.on('progress', function (e) {
        if (e.progress > 0.89) {
          currentProgress = 4

          if (oldProgress !== currentProgress) {
            if (oldProgress < currentProgress) {
              new TimelineMax()
                .set(['.pages-img1', '.pages-img2'], { opacity: 0 })
                .fromTo('.pages-img3', speed, { scale: 1, opacity: 1 }, { scale: 0.9, opacity: 0 })
                .fromTo('.pages-img4', speed, { opacity: 1, y: '100%' }, { opacity: 1, y: '0%' }, "-=0.7")
            }
          }
        } else if (e.progress > 0.55) {
          currentProgress = 3
          if (oldProgress !== currentProgress) {
            if (oldProgress < currentProgress) {
              new TimelineMax()
                .set(['.pages-img1', '.pages-img2'], { opacity: 0 })
                .fromTo('.pages-img2', speed, { scale: 1, opacity: 1 }, { scale: 0.9, opacity: 0 })
                .fromTo('.pages-img3', speed, { opacity: 1, y: '100%' }, { opacity: 1, y: '0%' }, "-=0.7")
            } else {
              new TimelineMax()
                .set(['.pages-img1', '.pages-img2'], { opacity: 0 })
                .fromTo('.pages-img4', speed, { opacity: 1, y: '0%' }, { opacity: 1, y: '100%' })
                .fromTo('.pages-img3', speed, { scale: 0.9, opacity: 0 }, { scale: 1, opacity: 1 }, "-=0.7")
            }
          }
        } else if (e.progress > 0.20) {
          currentProgress = 2
          if (oldProgress !== currentProgress) {
            if (oldProgress < currentProgress) {
              new TimelineMax()
                .set(['.pages-img1', '.pages-img4'], { opacity: 0 })
                .fromTo('.pages-img1', speed, { scale: 1, opacity: 1 }, { scale: 0.9, opacity: 0 })
                .fromTo('.pages-img2', speed, { opacity: 1, y: '100%' }, { opacity: 1, y: '0%' }, "-=0.7")
            } else {
              new TimelineMax()
                .set(['.pages-img1', '.pages-img4'], { opacity: 0 })
                .fromTo('.pages-img3', speed, { opacity: 1, y: '0%' }, { opacity: 1, y: '100%' })
                .fromTo('.pages-img2', speed, { scale: 0.9, opacity: 0 }, { scale: 1, opacity: 1 }, "-=0.7")
            }
          }
        } else {
          currentProgress = 1
          if (oldProgress !== currentProgress) {
            if (oldProgress > currentProgress) {
              new TimelineMax()
                .set(['.pages-img3', '.pages-img4'], { opacity: 0 })
                .fromTo('.pages-img2', speed, { opacity: 1, y: '0%' }, { opacity: 1, y: '100%' })
                .fromTo('.pages-img1', speed, { scale: 0.9, opacity: 0 }, { scale: 1, opacity: 1 }, "-=0.7")
            }
          }
        }

        if (oldProgress !== currentProgress) {
          oldProgress = currentProgress
        }
      })
    }
  }

}

export default CatalogSection
