import { select, selectAll } from '../libs/selectors'
import TweenMax from 'TweenMax'
import ScrollToPlugin from 'ScrollToPlugin'
import { getMq } from '../libs/helpers'
import 'animation.gsap'
import 'debug.addIndicators'

const scrollPlugin = ScrollToPlugin
function IntroSection(el, options = {}) {
  let section = document.querySelector(el)
  let inited = false
  let currentBreakpoint = getMq()
  let mediaTransformation
  return { init }

  function init() {
    if (section.length === 0 || inited) {
      return
    }

    inited = true

    section.classList.add('active')

    if (currentBreakpoint === 'desktop') {
      mediaTransformation = { delay: 0.2 }
    } else {
      mediaTransformation = { scale: 1.03, delay: 0.2 }
    }


    new TimelineMax().to(section.querySelector('.introduction-section__media'), 1.2, mediaTransformation)
      .from(select('.title', section), 1, { scale: 1.06, y: -10, opacity: 0 }, "-=1.2")
      .from(select('#discover-voice', section), 1, { y: 10, opacity: 0 }, "-=0.6")
      .from(select('#arrow2', section), 0.6, { y: 3, opacity: 0 }, "-=0.6")
      .from(select('#arrow1', section), 0.6, { y: 3, opacity: 0 }, "-=0.4")
      .from(select('span', section), 0.6, { opacity: 0 }, "-=0.4")

    select('#request-demo').addEventListener('click', function (e) {
      e.preventDefault()
      TweenLite.to(window, 1.5, {
        scrollTo: { y: "#contact-form", offsetY: 0, autoKill: false }, onComplete: function () {
          select('#input-company-name').focus()
        }
      })
    })

    select('#discover-voice').addEventListener('click', function (e) {
      e.preventDefault()
      TweenLite.to(window, 1, {
        scrollTo: { y: "#catalog-section", offsetY: 0, autoKill: false }
      })
    })
  }

}

export default IntroSection
